<template>
	<section class="businessPage">
		<main-title> </main-title>
		<fade-translate-transition-group tag="div" class="content">
			<article :key="index"
				:data-index="index"
				v-for="(paragraph, index) in paragraphs">
				<h2 :id="paragraph.id">{{ paragraph.title }}</h2>		
				<p v-html="paragraph.text" />		
				<p :key="counter"
					v-for="(additionalParagraph, counter) in paragraph.additionalParagraphs"
					v-html="additionalParagraph" />
			</article>
			<TheLadder :key="paragraphs.length" />
		</fade-translate-transition-group>
	</section>
</template>

<script>
	
	import SlideToLink from '@/components/SlideToLink';
	import MainTitle from '@/components/MainTitle';
	// import ConsultantTooltip from '@/components/ConsultantTooltip';
	import FadeTranslateTransitionGroup from '@/components/FadeTranslateTransitionGroup';
	
	import TheLadder from '@/components/TheLadder';

	export default {
		name: "BusinessPage",
		props: {
			className: {
				type: String,
				required: false,
				default: null
			},
			modifier: {
				type: String,
				required: false,
			},
		},
  	    components: {
  	    	SlideToLink,
  	    	FadeTranslateTransitionGroup,
  	    	MainTitle,
  	    	TheLadder
	    },
	    mixins: [],
	    data: () => ({
	    	paragraphs: [
		    	{
		    		title: ' ',
		    		id: "review",
		    		text: '  <strong>  </strong>  .  <strong> </strong>  ,   <strong> </strong> ,  <strong>  </strong>   <strong>  </strong>.'
		    	},
		    	{
		    		title: ' ',
		    		id: 'scores',
		    		text: ' <strong>  </strong>.       .   ,    <strong> </strong> (  ).  <strong>     </strong>. '
		    	},
		    	{
		    		title: ' ',
		    		id: 'market',
		    		text: '   <strong> </strong>,   <strong>  </strong>.  <i>37  </i>. <strong> </strong>   <strong>     </strong>.',
		    		additionalParagraphs: ['    ,    <strong>   </strong>.']
		    	},
		    	{
		    		title: ' ',
		    		id: 'team',
		    		text: '     ,   ,  <strong> </strong>   <strong>   </strong>.   <strong>       </strong>   ,   .',
		    		additionalParagraphs: [
		    			' <i>  22%</i>,  <i>  18%</i>,  <strong>  4%    </strong>. ',
		    			' ,  <strong>  22-   </strong> (  ),   <strong>      5%</strong>.'
		    		]
		    	},
	    	]
	    }),
	    beforeCreate() {
	    },
	    created() {
	    },
	    beforeMount () {
	    },
	    mounted() {
	    },
	    computed: {
	    },
	    methods: {
	    },
	    beforeUpdate() {
	    },
	    updated() {
	    },
	    beforeDestroy() {
	    }
	};
</script>

<style lang="sass">
	@import './../styles/conf/_sizes.sass'
	@import './../styles/conf/_colors.sass'
	@import './../styles/conf/_breakpoints.sass'
	.ladder

</style>


